<template>
    <el-container>
  <el-main>
<el-card class="box-card" shadow="hover">
<span class="title">智慧校园服务平台</span>
<form>
  <div class="demo-input-suffix">
  <el-input
    placeholder="请输入用户名(admin)"
    v-model="username">
    <i slot="prefix" class="el-input__icon el-icon-user"></i>
  </el-input>
  <el-input
    placeholder="请输入密码(admin)"
    v-model="password"
     show-password>
    <i slot="prefix" class="el-input__icon el-icon-s-goods"></i>
  </el-input>
  <el-row>
    <el-col :span="20">
<el-input
    placeholder="请输入验证码(请选择管理员登录)"
    v-model="verifiCode">
    <i slot="prefix" class="el-input__icon el-icon-picture-outline"></i>
  </el-input>
    </el-col>
    <el-col :span="4">
       <el-image
      style="width: 100px; height: 55px"
      @click="getCodeImg"
      :src="imgCodeUrl"></el-image>
    </el-col>
  </el-row>
  
  
  <el-row>
<el-col :span="8">
  <el-radio v-model="userType" label="1">管理员</el-radio>
  </el-col>
  <el-col :span="8">
  <el-radio v-model="userType" label="2">学生</el-radio>
  </el-col>
  <el-col :span="8">
  <el-radio v-model="userType" label="3">教师</el-radio>
  </el-col>
  </el-row>
  
</div>
<el-row :gutter="20">
  <el-col :span="12" :offset="9">
<el-button @click="loginFn" type="primary">登录</el-button>
  </el-col>
</el-row>
    </form>
    </el-card>

  </el-main>
</el-container>
</template>

<script>
import API from "../utils/service"
export default {
    data(){ 
      return{
        username: '',
        password: '',
        userType: 0,
        verifiCode: '' ,
        imgCodeUrl: '',
    }
    },
    mounted(){
      this.getCodeImg()
    },
   methods: {
    loginFn(){
      API({
        url:'/system/login',
        data: {
          username: this.username,
          password: this.password,
          userType: this.userType,
          verifiCode: this.verifiCode
        },
        method: 'post',
      }).then((response)=>{
        if(response.data.code===200){
          this.$store.commit('set_token',response.data.data.token)
          const token = this.$store.getters.get_token
    API({
        url:'/system/getInfo',
        headers: {
          token
        },
        method: 'get',
      }).then((response)=>{
        if(response.data.code===200){
          const userType = response.data.data.userType
          if(userType===1){
            console.log(response.data.data.user);
            this.$store.commit('store_info',response.data.data.user)
            this.$router.push({path:'/admin'})
          }else if(userType===2){
            this.$router.push({path:'/student'})
          }else if(userType===3){
            this.$router.push({path:'/teacher'})
          }
        }
        })
        }
        })
    },
    getCodeImg(){
      this.time = Date.parse(new Date());
      API({
        url:'/system/getVerifiCodeImage',
        params: {
          t: this.time
        },
        method: 'get',
        responseType: "arraybuffer"
      }).then((response)=>{
        return 'data:image/jpeg;base64,'+btoa(new Uint8Array(response.data).reduce((data,byte)=>data + String.fromCharCode(byte),''))
      }).then(res=>{
        this.imgCodeUrl = res;
      })
    }

   }
}
</script>

<style lang="scss" scoped>
  .el-container{
    background-image: url('@/upload/bg2.jpg');
    background-size: cover;
    background-position: 0 -200px;
    background-repeat: no-repeat;
  }
  .el-input__icon {
    color: #f391a9;
  }

  .el-input{
    margin: 10px 0;
  }
  .el-main {
    // background-color: #deab8a;
    
    color: #333;
    height: 100%;
  }

   .el-input ::v-deep .el-input__inner {
  color: #2e3035;
  background-color: #ffffffb0;
}
   .el-input ::v-deep .el-input__inner::placeholder {
  color: #df9464;
}
     
  .title {
    font-size: 30px;
    margin-left: 140px;
    text-align: center;
    line-height: 60px;
    vertical-align: center;
    color: #f391a9;
    font-weight: 800;
  }

  .box-card {
    width: 600px;
    margin: 0 auto;
    margin-top: 200px;
    margin-bottom: 400px;
    background-color: #ffffff80;
    backdrop-filter: blur(3px);
  }
.el-aside,.el-container,.common-layout,#app,body,html{
        height: 100%;
    }
  
</style>